const targetExts = ['.md', '.mdx'];

export function getPostUri(file: string) {
  for (const ext of targetExts) {
    if (file.endsWith(ext)) {
      file = file.slice(0, file.length - ext.length);
      break;
    }
  }
  if (!file.startsWith('/')) {
    file = '/' + file;
  }
  if (file.startsWith('/blog')) {
    file = file.slice(5);
  }
  return file;
}
