import React from 'react';
import { graphql, PageProps } from 'gatsby';

import Box from '@mui/joy/Box';
import AppContainer from '../layouts/AppContainer';
import ContentContainer from '../components/ContentContainer';
import BlogArchive, { PostType } from '../components/page/BlogArchive';

export default function IndexPage({ data }: PageProps<Queries.IndexPageQuery>) {
  const { title, description } = data.site.siteMetadata;

  return (
    <AppContainer>
      <ContentContainer>
        <BlogArchive posts={data.allMarkdownRemark.edges as unknown as Array<PostType>} />
      </ContentContainer>
    </AppContainer>
  );
}

export const query = graphql`
  query IndexPage {
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }, filter: { frontmatter: { publish: { eq: true } } }) {
      totalCount
      edges {
        node {
          id
          excerpt
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
            tags
          }
          parent {
            ... on File {
              relativePath
            }
          }
        }
      }
    }
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`;
